export function view(state, dispatch) {
    const matchValue = state.model;
    switch (matchValue.tag) {
        case 1:
            return todoView(state.definition, matchValue.fields[0], dispatch);
        case 2:
            return marqueeView(state.definition, matchValue.fields[0], dispatch);
        case 3:
            return colorCyclerView(state.definition, matchValue.fields[0], dispatch);
        case 4:
            return quoteView(state.definition, matchValue.fields[0], dispatch);
        case 5:
            return reactionView(state.definition, matchValue.fields[0], dispatch);
        case 6:
            return petView(state.definition, matchValue.fields[0], dispatch);
        case 7:
            return pixelView(state.definition, matchValue.fields[0], dispatch);
        case 8:
            return starView(state.definition, matchValue.fields[0], dispatch);
        case 9:
            return waveView(state.definition, matchValue.fields[0], dispatch);
        case 10:
            return djView(state.definition, matchValue.fields[0], dispatch);
        default:
            return counterView(state.definition, matchValue.fields[0], dispatch);
    }
}

